body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
    padding: 0px;
}

#root, #main {
    height: 100%;
    width: 100%;
    padding: 0px;
}
    
.board-row:after {
    clear: both;
    content: "";
    display: table;
}

.cell {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 24px;
    font-weight: bold;
    line-height: 34px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    height: 4vh;
    width: 2vw;
}
  
.cell:focus {
    outline: none;
}
  
.kbd-navigation .cell:focus {
    background: #ddd;
}

.board {
    padding-top: 35px;
}

/* Navigation bar css*/
.nav {
    overflow: hidden;
    background-color: #333;
    font-family: Arial;
    width: 100%;
}

.navbar p {
    float: left;
    font-size: 16px;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
}

.dropdown {
    float: left;
    overflow: hidden;
}

.dropdown .dropbtn {
    font-size: 16px;
    border: none;
    outline: none;
    color: white;
    padding: 14px 16px;
    background-color: inherit;
    font-family: inherit; /* Important for vertical align on mobile phones */
    margin: 0; /* Important for vertical align on mobile phones */
}

.navbar a:hover, .dropdown:hover .dropbtn {
    background-color: red;
}

.dropdown-content {
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
}

.dropdown-content p {
    float: none;
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
    text-align: left;
}

.dropdown-content a:hover {
    background-color: #ddd;
}
  
/* Show the dropdown menu on hover */
.dropdown:hover .dropdown-content {
    display: block;
}




  
  
  